import "./App.css";
import React from "react";
import Author from "./pages/Author";
import Home from "./pages/Home";
import ButtonImagePage from "./buttonImagePage";
import { Routes, Route, useNavigate } from "react-router-dom";
import { Link } from "react-router-dom";

function App() {
  let Component;
  const link = window.location.pathname;
  console.log("window.location.pathname is " + link);
  console.log("redirection attempt 1");
  Component = null;
  const navigate = useNavigate();
  switch (window.location.pathname) {
    case "/":
    case "/dog-model/":
    case "/home":
      Component = <Home />;
      break;
    case "/author":
      Component = <Author />;
      break;
    default:
      console.log("couldnt find page for " + link);
      Component = <Home />;
  }
  // return <div className="App">{Component}</div>;
  return (
    <div>
      <button onClick={() => navigate("/")}>Home</button>
      {/* <button onClick={() => navigate("/author")}>Author</button> */}
      {/* <Link to="/author">
        <p>Author Link Attempt</p>
      </Link> */}
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/author" element={<Author />} />
      </Routes>
    </div>
  );
}

export default App;
